<script>
import LeadDataService from "../../../services/DataService";
import crmtimeline from "../../../components/Common/Modal/crmtimeline.vue";
import crmnote from "../../../components/Common/Modal/crmnote.vue";
import crmattachment from "../../../components/Common/Modal/crmattachment.vue";
import modaltask from "../../../components/Common/Modal/modaltask.vue";
import modalevent from "../../../components/Common/Modal/modalevent.vue";
import modalcall from "../../../components/Common/Modal/modalcall.vue";
import modalattachment from "../../../components/Common/Modal/modalattachment.vue";
import modalexistingcontact from "../../../components/Common/Modal/modalexistingcontact.vue";
import modalcontact from "../../../components/Common/Modal/modalcontact.vue";
import modalemail from "../../../components/Common/Modal/modalemail.vue";
export default {
    components: {
        crmtimeline,
        crmnote,
        crmattachment,
        modaltask,
        modalevent,
        modalcall,
        modalattachment,
        modalexistingcontact,
        modalcontact,
        modalemail
    },
    data() {
        return {
            activeKey: 1,
            isNotes: false,
            isAttachment: false,
            isTimeline: false,
            isTask: false,
            LeftViewData: [],
            RightViewData: null,
            activitiesData: null,
           productHeaders: null,
            noRecordForTab: false,
            showAttachmentModal: false,
            showExistingContactModal: false,
            showContactModal: false,
            showConvertLead: false,
            showProductModal: false,
            showAddTaskModal: false,
            showAddEventModal: false,
            showCallModal: false,
            sendEmailModal: false,
            stageName: '--',
            timelinedata: [],
            noteData: [],
            EmailData: [],
            contactsData: [],
            contactsHeader: [],
            attachmentData: [],
             modalItem: null,
            modalTaskItem: null,
            modalFollowUpItem: null,
            modalCallItem: null,
            modalDeleteItem: null,
            isView: false,
            modal: null,
            productData: null,
            NoteListType: "list",
            SortBy: '',
            SortExp: '',
            PageNumber: 1,
            PageSize: 50,
            userid: null,
            companyId: null,
            stausName:null,
            containername: null,
            ApiURLEmail: `id=0&refId=${this.$route.params.id}&subModuleCode=${this.$route.params.subModuleCode}`,
            ApiURL: `id=${this.$route.params.id}&moduleName=${this.$route.params.moduleName}&subModuleCode=${this.$route.params.subModuleCode}`,
            maxsize:
                "@TALYGEN.Models.ConfigurationRepository.GetConfigurationValueByKey(companyId, userid, 'CRM', 'CRMPerFileSize')",
            disallowedfiletypes:
                "@TALYGEN.Models.ConfigurationRepository.GetConfigurationValueByKey(companyId, userid,'CRM', 'CRMDisallowedFileTypes')",
            isShowNotePopup: false,
            editUrl: `/CRM/ManageStage/${this.$route.params.id}/${this.$route.params.moduleName}/${this.$route.params.subModuleCode}`,
            isLoading:false
        }
    },
    created: function () {
        this.userid = this.GetUserInfo.ID;
        this.companyId = this.GetUserInfo.CompanyId;
        this.containername = this.GetUserInfo.ContainerName;
        this.FetchData();
    },

    methods: {
        DisableButton: function () {
            var vm = this;
            vm.isTimeline = false;
            vm.isNotes = false;
            vm.isAttachment = false;
            vm.isTask = false;
        },
        GetLocalizedValue: function (fieldName, displayName) {
            if (this.isBlank(displayName)) {
                var chk = this.$t(String(fieldName).toUpperCase());
                if (chk || /^\s*$/.test(chk)) {
                    return chk;
                }
                chk = this.$t(String(fieldName));
                if (chk || /^\s*$/.test(chk)) {
                    return chk;
                }
                return fieldName;
            }
            else {
                var chk = this.$t(String(displayName).toUpperCase());
                if (chk || /^\s*$/.test(chk)) {
                    return chk;
                }
                chk = this.$t(String(displayName));
                if (chk || /^\s*$/.test(chk)) {
                    return chk;
                }
                return displayName;
            }
        },
        formatDate: function (value) {
            if (value) {
                return moment(value, String(Globalize.culture().calendar.patterns.d).toUpperCase()).format(String(Globalize.culture().calendar.patterns.d).toUpperCase())
            }
        },
        DeleteActivityData: function (id) {
            var vm = this;
            if (id > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, null, function () {
                    var postJSON = {
                        ids: id,
                        moduleName: "CRM",
                        subModuleCode: "CRM_STAGES",
                        refCode: 'DELETE_VIEW_ACTIVITY',
                        refId: vm.$route.params.id,
                    };
                    var objectPostString = JSON.stringify(postJSON);
                    LeadDataService.CommonDelete(objectPostString).then(function (response) {
                      if (response.data[0].Status == "Success") {
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Activity')]), "SUCCESS", true, vm.$t("Alert"));
                            vm.loadData("activities");
                        }
                        else {
                            vm.ShowAlert(vm.$t('DeletDeletedErroredSuccess', [vm.$t('Activity')]), "warning", true, vm.$t("Alert"));
                        }
                    },);

                });
            }
        },
        FetchData: function () {
            var vm = this;
            vm.isLoading=true;
            LeadDataService.FormLayoutView(vm.ApiURL)
                .then(function (response) {
                    vm.isLoading=false;
                    var fullLeftJSON = response.data.LEFTDATA;
                    fullLeftJSON = $.grep(fullLeftJSON, function (element, index) {
                        return element.form_field_visibility == 'YES';
                    });
                    if (response.data.RIGHTDATA != null && response.data.RIGHTDATA.length > 0) {
                        var fullJSON = response.data.RIGHTDATA;
                        var gruopBy = "";
                        fullJSON.forEach(item => {
                            gruopBy = vm.$options.filters.formatDate(item.Complete_datetime);
                            var chkGroup = $.grep(vm.timelinedata, function (element, index) {
                                return element.groupKey == gruopBy;
                            });
                            if (chkGroup.length == 0) {
                                var returnedData = $.grep(fullJSON, function (element, index) {
                                    return vm.$options.filters.formatDate(element.Complete_datetime) == gruopBy;
                                });
                                if (returnedData.length > 0) {
                                    var objGroup = {
                                        groupKey: gruopBy,
                                        groupData: returnedData,
                                    };
                                    vm.timelinedata.push(objGroup);
                                }
                            }
                        });
                        
                    } else {
                        vm.FetchData();
                        vm.isLoading=false;
                        vm.noRecordForTab = true;
                    }
                    if (fullLeftJSON != null && fullLeftJSON.length > 0) {
                        var groupId = fullLeftJSON[0].group_id;
                        $.each(fullLeftJSON, $.proxy(function (index, item) {
                            if (item.name == 'stage_name') {
                                vm.stageName = item.value;
                            }
                            var chkGroup = $.grep(vm.LeftViewData, function (element, index) {
                                return element.group_id == groupId;
                            });
                            if (chkGroup.length == 0) {
                                var returnedData = $.grep(fullLeftJSON, function (element, index) {
                                    return element.group_id == groupId;
                                });
                                if (returnedData.length > 0) {
                                    var objGroup = {
                                        group_id: groupId,
                                        group_name: returnedData[0].group_name,
                                        LeftViewData: returnedData
                                    };
                                    vm.LeftViewData.push(objGroup);
                                }
                            }
                            groupId = item.group_id;
                        }, vm));
                    }
                  
                    setTimeout(function () { vm.CheckBoxBootstrap() }, 100);
                    setTimeout(function () { vm.RadioButtonBootstrap() }, 500);
                })
        },
        openAttachmentDialogue() {
            this.isShowNoteDialogue = (this.isShowNoteDialogue) ? false : true;
        },
        openAddTaskModel() {
            this.showAddTaskModal = (this.showAddTaskModal) ? false : true;
        },
        openAddNewEvent() {
            this.showAddEventModal = (this.showAddEventModal) ? false : true;
        },
        openCallEvent() {
            this.showCallModal = (this.showCallModal) ? false : true;
        },
        openEmailModal(item) {
            this.sendEmailModal = true;
            this.modalItem = item;
        },
        openActivityModal(item) {
            if (item.activity_type == "Task") {
                this.showAddTaskModal = true;
                this.modalTaskItem = item;
            }
            else if (item.activity_type == "Follow Up" || item.activity_type == "FollowUp") {
                this.showAddEventModal = true;
                this.modalFollowUpItem = item;
            }
            else if (item.activity_type == "Call") {
                this.showCallModal = true;
                this.modalCallItem = item;
            }
        },
        FilterDDLandNormalValue: function (item) {           
            if (item && item.length > 0) {
                try {
                    return JSON.parse(item)[0].name;
                } catch (ex) {
                    return item;
                }
            } else {
                return "";
            }
        },
        DeleteActivity(item) {
            this.modalDeleteItem = item;
            this.DeleteActivityData(this.modalDeleteItem.followup_notification_id);
        },
        loadData: function (tabName, callFrom) {
            var vm = this;
            vm.DisableButton();
            if (tabName == "notes") {
                vm.isNotes = true;
                vm.activeKey = 2;
                if (callFrom && callFrom == "savenote") vm.isShowNotePopup = false;
            }
            else if (tabName == "attachments") {
                vm.isAttachment = true;
                vm.activeKey = 3;
            }
            else if (tabName == "activities") {
                vm.isTask = true;
                vm.isTimeline = true;
                vm.activeKey = 4;
            }
            if (tabName != "" && tabName != null) {
                var url = '';
                if (tabName == "attachments") {
                    LeadDataService.AttachmentListing(vm.ApiURL).then(function (response) {
                        if (response.data != null) {
                            vm.attachmentData = response.data.data;
                            setTimeout(function () { vm.CheckBoxBootstrap() }, 100);
                            vm.noRecordForTab = false;
                        } else {
                            vm.noRecordForTab = true;
                        }
                    });
                }
                else if (tabName == "timeline") {
                    url = `id=${vm.$route.params.id}&subModuleCode=${vm.$route.params.subModuleCode}`;
                    LeadDataService.TimelineListing(url).then(function (response) {
                        if (response.data != null) {
                            vm.timelinedata = [];
                            var fullJSON = response.data.data;
                            var gruopBy = "";
                            $.each(fullJSON, $.proxy(function (index, item) {
                                gruopBy = vm.$options.filters.formatDate(item.Complete_datetime);
                                var chkGroup = $.grep(vm.timelinedata, function (element, index) {
                                    return element.groupKey == gruopBy;
                                });
                                if (chkGroup.length == 0) {
                                    var returnedData = $.grep(fullJSON, function (element, index) {
                                        return vm.$options.filters.formatDate(element.Complete_datetime) == gruopBy;
                                    });
                                    if (returnedData.length > 0) {
                                        var objGroup = {
                                            groupKey: gruopBy,
                                            groupData: returnedData
                                        };
                                        vm.timelinedata.push(objGroup);
                                    }
                                }
                            }, vm));
                            setTimeout(function () { vm.CheckBoxBootstrap() }, 100);
                            vm.noRecordForTab = false;
                        } else {
                            vm.noRecordForTab = true;
                        }
                    });
                    vm.activeKey = 1;
                }
                else if (tabName == "notes") {
                    this.noteData = [];
                    LeadDataService.NoteListing(vm.ApiURL).then(function (response) {
                        if (response.data != null && response.data.data.length > 0) {
                            vm.noteData = response.data.data;
                            setTimeout(function () { vm.CheckBoxBootstrap() }, 100);
                            vm.noRecordForTab = false;
                        } else {
                            vm.noRecordForTab = true;
                        }
                    });
                }
                else if (tabName == "activities") {
                    LeadDataService.ActivityListing(vm.ApiURL).then(function (response) {
                        if (response.data != null && response.data.data.length > 0) {
                            vm.activitiesData = response.data.data;
                            setTimeout(function () { vm.CheckBoxBootstrap() }, 100);
                            setTimeout(function () { vm.ResponsiveDataTable('table-openactivities-content') }, 500);
                            vm.noRecordForTab = false;
                        } else {
                            vm.noRecordForTab = true;
                            vm.contactsData = null;
                        }
                    });
                }
            }
        },
        openShowNotePopup: function () {
            var ths = this;
            if (
                typeof ths.checkForAdditionalPermission !== "undefined" &&
                !ths.checkForAdditionalPermission("AddNote")
            ) {
                this.ShowAlert(
                    ths.$t("additionalOwnerAuthorityWarning"),
                    "warning",
                    true,
                    ths.$t("Alert")
                );
            } else {
                ths.isShowNotePopup = true;
                $("#tab-notes").trigger("click");
                ths.loadData("notes");
            }
        },
        openCRMAttachmentModal: function () {
            if (
                typeof this.checkForAdditionalPermission !== "undefined" &&
                !this.checkForAdditionalPermission("AddAttachment")
            ) {
                alertR(
                    this.$t("additionalOwnerAuthorityWarning"),
                    true,
                    this.$t("Alert")
                );
            } else {
                this.showAttachmentModal =
                    this.showAttachmentModal ? false : true;
            }
        },
        downloadAttachment: function (item) {
        }
    },
    updated: function () {
        var vm = this;
        setTimeout(function () { vm.setHeaderForSelectedTab() }, 500);
    },

}
</script>

<template>
    <div id="divMainFormData" v-cloak>
        <section id="main-content" class="main-content">
            <loader :is-visible="isLoading"></loader>
            <section class="main-content">
                <div class="left-menu-tab div-click-event">
                    <div class="col-md-12 p-0 ">
                        <div class="theme-primary partition">
                            <span class="p-name text-white textelipsis">{{stageName}}</span>
                        </div>
                    </div>
                    <ul class="nav nav-pills flex-column left-tab mt-2 responivecustomtab" id="myTab" role="tablist">
                        <li class="nav-item">

                            <a class="nav-link left-menu" :class="{ 'active': activeKey == 1 }"
                                data-bs-target="#tab-lead-content" href="javascript:void(0)" data-bs-toggle="tab"
                                role="tab" id="tab-Details" v-on:click="loadData('timeline'); DisableButton()">
                                <em class="fa fa-list" aria-hidden="true" aria-selected="true"></em>
                                {{ $t('Detail') }}
                            </a>
                        </li>

                       
                    </ul>
                </div>
                <div class="right-content">
                    <div class="tab-pane mb-2">
                        <div class="theme-primary partition p-actions-expand">
                            <span class="p-name text-white" id="span_ForSelectedTabName"></span>
                            <span class="p-actions float-right">
                                <a href="javascript:;"
                                    v-bind:class="{ 'p-action-btn text-white': true, 'disabled': false }" title
                                    data-toggle-tooltip="tooltip" data-original-title="Card View"
                                    v-on:click="NoteListType = 'card'" v-if="isNotes && NoteListType == 'list'">
                                    <em class="fa fa-th-large"></em>
                                    <br />
                                    {{ $t('CardView') }}
                                </a>
                                <a href="javascript:;"
                                    v-bind:class="{ 'p-action-btn text-white': true, 'disabled': false }" title
                                    data-toggle-tooltip="tooltip" data-original-title="List View"
                                    v-on:click="NoteListType = 'list'" v-if="isNotes && NoteListType != 'list'">
                                    <em class="fa fa-list"></em>
                                    <br />
                                    {{ $t('ListView') }}
                                </a>
                                <a href="javascript:;"
                                    v-bind:class="{ 'p-action-btn text-white': true, 'disabled': false }" title
                                    id="ancaddnotesloaddata" data-toggle-tooltip="tooltip"
                                    data-original-title="Add Notes" v-on:click="openShowNotePopup()" v-if="isNotes">
                                    <em class="fa fa-plus"></em>
                                    <br />
                                    {{ $t('AddNotes') }}
                                </a>

                                <a href="javascript:;" v-bind:class="{'p-action-btn text-white':true}"
                                    :title="$t('UploadFile')" v-if="isAttachment"
                                    v-on:click="openCRMAttachmentModal(null);">
                                    <em class="fa fa-plus"></em>
                                    <br />
                                    {{ $t('Add') }} {{ $t('Attachment') }}
                                </a>
                                <a href="javascript:void(0)" v-bind:class="{'p-action-btn text-white btn_add':true}"
                                    title="" id="" v-on:click="openAddTaskModel()" data-original-title="Add Task"
                                    v-if="isTask"><em class="fa fa-plus"></em><br />{{ $t('AddTask') }}</a>
                                <a href="javascript:void(0)" v-bind:class="{'p-action-btn text-white btn_follow':true}"
                                    title="" id="" v-on:click="openAddNewEvent()" data-original-title="Follow Up"
                                    v-if="isTimeline"><em class="fa fa-share"></em><br /> {{ $t('FollowUp') }}</a>
                                <a href="javascript:void(0)" v-bind:class="{ 'p-action-btn text-white btn_call': true }"
                                    title id v-on:click="openCallEvent()" v-if="isTimeline"
                                    data-original-title="Schedule Call">
                                    <em class="fa fa-phone"></em>
                                    <br />
                                    {{ $t('Schedule') }} {{ $t('Call') }}
                                </a>
                                <RouterLink to="/CRM/StageListing" class="p-action-btn text-white float-right">
                                    <em class="fa fa-angle-double-left"></em>
                                    <br />
                                    {{ $t('BacktoList') }}
                                </RouterLink>
                                <RouterLink :to="editUrl" v-if="stausName != 'Closed'" class="p-action-btn text-white">
                                    <em class="fa fa-pencil"></em><br /> {{ $t('Edit') }}
                                </RouterLink>
                            </span>
                            <span class="collapse-head-btns">
                                <a href="javascript:;" class="collapse-btns"><em    class="fa fa-angle-up text-white"></em></a>
                            </span>
                        </div>
                    </div>
                    <div class="tab-pane mb-0">
                        <div class="tab-content fsmtabcontrolMobileView" id="myTabContent">
                            <div class="expand-list">
                                <a href="javascript:;"
                                    class="expand-list-btn justify-content-center align-items-center">
                                    <em class="icon-solid arrow-solid-left text-white" style="font-size:9pt"></em>
                                </a>
                            </div>
                            <div class="tab-pane p-0 pb-2 border-0" role="tabpanel" id="tab-detail-content"
                                aria-labelledby="tab-detail-content" :class="{ 'active show': activeKey == 1 }">

                                <div class="listing">
                                    <div class="row">
                                        <div class="col-md-12">
                                            <div class="card mb-3 probx">
                                                <div class="card-header">
                                                    <h2>{{$t("Details")}}</h2>
                                                    <div class="cont_right ">
                                                        <div class="bar">
                                                        </div>
                                                    </div>
                                                </div>
                                                <div class="card-body">
                                                    <div class="panel1" v-for="(group,groupIndex) in LeftViewData" :key="groupIndex">
                                                        <div class="heading-border">
                                                            <h5 class="h5 mb-2">
                                                                <span>{{$t(group.group_name,'')}}</span>
                                                            </h5>
                                                        </div>
                                                        <div class="row">
                                                            <div class="col-md-6 col-lg-3"
                                                                v-for="(item,index) in group.LeftViewData" :key="index">
                                                                <div class="form-group">
                                                                    <label>
                                                                        <strong>{{$t(item.name,
                                                                        item.display_name)}}:</strong>
                                                                        <span class="answer rightlabeltext ml-1"
                                                                            v-if="item.dt_code == 'date' || item.dt_code == 'datetime'">
                                                                            {{item.value | formatDate}}</span>
                                                                        <span class="answer rightlabeltext ml-1"
                                                                            v-if="item.dt_code == 'decimal'">{{isBlank(item.value)?"0.00":item.value | currency}}</span>
                                                                        <span class="answer rightlabeltext ml-1"
                                                                            v-if="item.dt_code != 'date' && item.dt_code != 'datetime'  && item.dt_code != 'decimal' && item.picklist_options != 'Lookup'">{{
                                                                            isBlank(item.value)?"--":(item.value)}}</span>
                                                                        <span class="answer rightlabeltext ml-1"
                                                                            v-if="item.dt_code != 'date' && item.dt_code != 'datetime'  && item.dt_code != 'decimal' && item.picklist_options == 'Lookup'">{{
                                                                            isBlank(item.value)?"--":(FilterDDLandNormalValue(item.value))}}</span>
                                                                    </label>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                            <div class="card mb-3 probx">
                                                <div class="card-header">
                                                    <h2>{{$t("Timeline")}}</h2>
                                                </div>
                                                <div class="card-body">
                                                    <crmtimeline v-bind:timelinedata="timelinedata"></crmtimeline>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>

                            <div class="tab-pane fade p-0 pb-2 border-0" id="tab-notes-content" role="tabpanel"
                                aria-labelledby="tab-notes-content" :class="{ 'active show': activeKey == 2 }">
                                <crmnote v-if="noteData" v-bind:notedata="noteData" :userid="userid"
                                    :companyid="companyId" :sourceid="$route.params.id"
                                    :modulename="$route.params.moduleName" :submodulecode="$route.params.subModuleCode"
                                    v-bind:listtype="NoteListType" :showNotePopUp="isShowNotePopup"
                                    callfrom="quickViewAndNoteJS"></crmnote>
                            </div>
                            <div class="tab-pane fade p-0 pb-2 border-0" id="tab-attachment-content" role="tabpanel"
                                aria-labelledby="tab-attachment-content" :class="{ 'active show': activeKey == 3 }">
                                <crmattachment v-bind:attachmentdata="attachmentData" :sourceid="$route.params.id">
                                </crmattachment>
                            </div>
                            <div class="tab-pane fade p-0 border-0 listing" id="tab-openactivities-content"
                                role="tabpanel" aria-labelledby="tab-openactivities-content"
                                :class="{ 'active show': activeKey == 4 }">
                                <div class="table-responsive" v-if="!noRecordForTab">
                                    <table class="table table-bordered mb-0" id="table-openactivities-content" aria-describedby="activitiescontent">
                                        <thead class="thead-bg">
                                            <tr>
                                                 <th scope="col">
                                                    <span>{{ $t('Subject') }}</span>
                                                </th>
                                                 <th scope="col">
                                                    <span>{{ $t('Activity') }} {{ $t('Type') }}</span>
                                                </th>
                                                 <th scope="col">
                                                    <span>{{ $t('Activity') }} {{ $t('ScheduleDate') }}</span>
                                                </th>
                                                 <th scope="col">
                                                    <span>{{ $t('Activity') }} {{ $t('OWNER') }}</span>
                                                </th>
                                                 <th scope="col">
                                                    <span>{{ $t('CreatedAt') }}</span>
                                                </th>
                                                 <th scope="col">
                                                    <span>{{ $t('Status') }}</span>
                                                </th>
                                                 <th scope="col" class="text-center">
                                                    <span>{{ $t('Action') }}</span>
                                                </th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr v-for="(item,itemIndex) in activitiesData" :key="itemIndex">
                                                <td><a href="javascript:;"
                                                        v-on:click="openActivityModal(item);">{{item.followup_notification_subject}}</a>
                                                </td>
                                                <td>
                                                    <div v-if="item.activity_type === 'FollowUp'">
                                                        Follow up
                                                    </div>
                                                    <div v-else>
                                                        {{item.activity_type}}
                                                    </div>
                                                </td>
                                                <td>{{item.followup_notification_schedule | formatDate}}
                                                </td>
                                                <td>{{item.activity_owner}}</td>
                                                <td>{{item.created_at | formatDate}}</td>
                                                <td><span
                                                        v-bind:class="{'px-2 bg-success text-white d-block text-center': (item.status_name == 'Active'),'px-2 bg-danger text-white d-block text-center': (item.status_name == 'Inactive')}">{{item.status_name}}</span>
                                                </td>
                                                <td class="text-center single-action"><a href="javascript:;"
                                                        title="Delete" v-on:click="DeleteActivity(item);"><em                        class="fa fa-trash action-icon text-danger"></em></a>
                                                </td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                                <div v-if="noRecordForTab" class="text-center text-danger p-4 border no-record">
                                    {{$t("NoRecordfound")}}</div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        </section>
